const { registerBlockType } = wp.blocks;

registerBlockType('toolkit/carousel',{

  // built-in attributes
  title: 'Carousel',
  descriptions: 'Block to generate a carousel.',
  icon: 'format-image',
  category: 'layout',
  
  // costum attributes
  attributes: {
    author: {
      type: 'string'
    }
  },

  // costum functions

  // built-int functions
  // What you will see in backoffice
  edit({ attributes, setAttributes }) {

    // Function to save attributes
    function updateAuthor(event) {
      setAttributes({ author: event.target.value });
    }

    return <input value={attributes.author} onChange={ updateAuthor } type="text" />;
  },

  // What you will see in frontoffice
  save({ attributes }) {
    return <div>{ attributes.author }</div>;
  }
});